<script>
export default {
  props: ['role'],
  data: () => ({
    isRotate: false
  }),
  computed: {
    cardBgColor () {
      const { role } = this
      return role === 'red' || role === 'sherif' ? '#f02828' : '#181818'
    },
    cardType () {
      const { role } = this
      return role === 'sherif' ? 'Շերիֆ' : role === 'don' ? 'Դոն' : ''
    }
  },
  methods: {
    Rotate () {
      if (this.isRotate) return

      this.isRotate = !this.isRotate

      setTimeout(() => {
        this.isRotate = !this.isRotate
      }, 2000);
      // setTimeout(() => {
      //   console.log('something')
      // }, 3500);
    }
  }
}
</script>

